.articles__filters {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  list-style: none;
  margin: 0;
  padding-left: 0;
  width: 100%;
}

.articles__filters__filter {
  -ms-flex-align: center;
      align-items: center;
  color: #523996;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 40px;
  height: 100px;
  -ms-flex-pack: center;
      justify-content: center;
  opacity: 0.5;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-transition: opacity 0.3s ease-in-out;
  -o-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
  -webkit-transition: background-color 0.3s ease-in-out;
  -o-transition: background-color 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out;
}

.articles__filters__filter--active {
  background-color: #523598;
  border: none;
  color: #fff;
  opacity: 1;
  outline: none;
}

.articles__filters__filter:hover {
  opacity: 1;
}

@media screen and (max-width: 768px) {
  .articles__filters__filter {
    font-size: 18px;
  }
}



